html,
body {
	overflow-x: hidden !important;
	min-height: 100%;
	background: #ffffff;
	font-weight:200;
	overflow-y:scroll;
	-webkit-overflow-scrolling: touch;
    overflow: auto;
}

html.item_ajax_open, body.item_ajax_open{
	overflow:hidden;
	position:relative;
}

a {
	color: #428bca;
}

a:hover,
a:active,
a:focus {
	outline: none;
	text-decoration: none;
	color: #72afd2;
}

/* LAYOUT */
.wrapper {
	min-height: 100%;
}

.wrapper:before,
.wrapper:after {
	display: table;
	content: " ";
}

.wrapper:after {
	clear: both;
}

body .wrapper {
	margin-top: 60px;
}
.headerMenu .nav.navbar-nav{
	display:block;
}
.navbar-nav > li a {
	border-radius: 0px;
	margin: 0;
	color: #6C7E8A;
}
.navbar-nav > li a:hover {
	color: #111111;
	background: transparent;
}
/*HEADER*/

.headerSocial{
	background-color: #151515;
    border-bottom: 0px;
    background-size: cover;
    background-position: top center;
}
.topHeadline{
	float:left; 
	line-height:40px; 
	color:#EEEEEE; 
	margin:0px; 
	text-transform:uppercase; 
	font-size:11px;
}
.top_bar{
	ul{
		display: inline-block;
		float: right;
		overflow: hidden;
		text-align: right;
		min-height: 40px;
		padding: 0;
		margin-bottom:0px;
		li{
			float: left;
			list-style: none;
			display: inline-block;
			height: 40px;
			a{
				display: inline-block;
				line-height: 40px;
				color: #dddddd;
				font-size:12px;
				padding-right:15px;
			}
		}
	}
}
.footer_menu{
	float:right;
	li{
		a{
			padding:10px;
			line-height:18px;
			color:#ddd;

			&:hover, &:focus, &:active{
				color:#fff;
			}
		}
	}
}
.social{
	display: inline-block;
	float: right;
	overflow: hidden;
	text-align: right;
	min-height: 40px;
	padding: 0;
	margin: 0;
}
.social li{
	float: left;
	list-style: none;
	display: inline-block;
	height: 40px;
}
.social li a{
	display: inline-block;
	line-height: 40px;
	color: #dddddd;
	font-size:12px;
}
footer .social li a.socialIcon{
	line-height:10px;
}
.social li a:hover{
	color: #ffffff;
	text-decoration: none;
}
.about,.contact{
	margin: 0 5px;
	padding: 0 5px;
}
.contact{
	margin-right:15px;
}
.about:hover,.contact:hover{
	text-decoration: underline;
}

.btn-search i{
	position:relative;
	top:-4px;
	font-size:16px;
	color:#fff;
}

.btn-search{
	background:rgba(0, 0, 0, 0.6) !important;
	border:0px !important;
	box-shadow:0px 0px 0px !important;
	float:right;
}

#search-form{
	display:none;
	margin-top:10px;
}

#search-toggle{
	margin-top:5px;
}

#search-toggle .fa-close{
	display:none;
}

#search-form .form-group.is-focused .form-control{
    background-image: linear-gradient(#ffffff, #ffffff), linear-gradient(#ffffff, #ffffff);
}

.socialIcon{
	width: 40px;
	height: 40px;
	line-height:0px;
	text-align:center;
	border-right:1px solid #292929;
	-webkit-transition:background 0.3s linear;
	        transition:background 0.3s linear;
}

.socialIcon.facebook{
	border-left:1px solid #292929;
}

.socialIcon:hover{
	background:none;
	-webkit-transition:background 0.3s linear;
	        transition:background 0.3s linear;
}

.socialIcon:hover i{
	color:#fff;
	-webkit-transition:color 0.3s linear;
	        transition:color 0.3s linear;
}

.socialIcon i{
	line-height:0px;
	color:#ccc;
	font-size:14px;
	-webkit-transition:color 0.3s linear;
	        transition:color 0.3s linear;
}

.navbar .navbar-brand {
	float: left;
	line-height: 50px;
	padding-bottom: 3px;
    padding-top: 7px;
	width: auto;
	height: 50px;
	display: inline-block;
	min-width: auto;
	min-height: 50px;
	margin: 0px;
}

.navbar-nav{
	float:right;

}

.navbar, .navbar.navbar-default{
	border-radius:0px;
}

.navbar-brand img{
	width:auto;
	height:100%;
}
.headerMenu .menu{
	border-bottom: 1px solid #e1e9f1;
	width: 100%;
	display: inline-block;
}
.navRight{
	float: right;
	display: inline-block;
	line-height:63px;
}
.headerMenu .nav.navbar-nav{
	display: inline-block;
	float: left;
	border-bottom: 0 none;
	margin-bottom:0px;
}
.headerMenu .navbar-nav>li>a{
	font-weight: normal;
    font-size: 14px;
    text-align: left;
    display: inline-block;	
	border: 0 none;
    padding: 24px 20px;
    margin: 0px 0px -1px;
    border-bottom: 1px solid transparent;
    transition: border 0.2s ease;
    line-height: 58px;
}
.headerMenu .navbar-nav>li>a:hover{
	color: #e04d32;
	border: 0 none;
	background: transparent;
    margin: 0px 0px -1px;
    border-bottom: 1px solid #e04d32;
    transition: border 0.5s ease;
}
.headerMenu .navbar-nav>li.active>a{
	border: 0 none;
	background: transparent;
	color: #ea1042;
    border-bottom: 1px solid #e04d32;
    padding: 30px 0px;
    margin: 0px 20px -1px;
}

.nav .open>a, .nav .open>a:hover, .nav .open>a:focus{
	border-color:#e04d32 !important;
}

.headerMenu .glyphicon.glyphicon-search{
	font-size: 20px;
}

.headerMenu .dropdown:hover .dropdown-menu{
	display: block;
	visibility: visible;
    opacity: 1;
    transition: opacity .5s, transform .5s;
    transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1) !important;
    transform: scale3d(1, 1, 1) translate3d(0, 0, 0) !important;
	
}
.headerMenu .dropdown .dropdown-menu{
	top: 80%;
	border-radius: 0px !important;
	border:0px;
	padding:15px;
	display:block;

	margin-top: 10px !important;
	  visibility: hidden;
	  border: 0 none !important;
	  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25) !important;
	  opacity: 0;
	  transition: opacity .5s, transform .5s, visibility 0s .5s;
	  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1) !important;
	  transform: scale3d(0.95, 0.95, 1) translate3d(-3px, -10px, 0px) !important;
	  transform-origin: 100% 0 !important;
}

.headerMenu .dropdown li a, .dropdown-menu li > a:hover, .dropdown-menu li > a:focus{
	box-shadow:0px 0px 0px;
}

.navbar .navbar-nav > li > .dropdown-menu {
    margin-top: -6px;
}

.dropdown-menu > li > a{
	padding:6px 10px;
}
.mobile-menu{
	display:none;
}
.searchIcon{
	float: left;
	display: inline-block;
}
.searchIcon a{
	display: inline-block;
    padding: 19px 0px;
    margin: 0px 0 -1px 20px;
    color: #6C7E8A;
    border-bottom: 0px;
    padding-top: 24px;
}
.searchIcon a:hover{
    border-bottom: 1px solid #e04d32;
}
.searchIcon i{
	color: #6C7E8A;
}
/*FOOTER*/
.footer {
	border-top: 1px solid #eaeaea;
	color: #6C7E8A;
	background:#232329;
}
.footer p{
