/*ITEMS*/
#downloads{
	width: 100%;
	margin-top: 20px;
	overflow: hidden;
}
.item_larger{
	display: inline-block;
	margin: 18px 0;
}
.item{
	border: 0px;
    box-shadow: 0px 1px 1px rgba(200, 200, 200, 0.2);
    background: #fff;
    border-radius: 3px;
    position: relative;
    border: 0px solid #f1f1f1;
    position: relative;
    width: 100%;
    margin-bottom: 30px;
    border-radius: 3px;
    color: rgba(0,0,0, 0.87);
    background: #fff;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    -webkit-transition: all 0.3s linear; /* Safari and Chrome */ /* Firefox */ /* IE 9 */ /* Opera */
    transition: all 0.3s linear;
    top:0px;
}

.item:hover{
	top:-5px;
	-webkit-transition: all 0.3s linear; /* Safari and Chrome */ /* Firefox */ /* IE 9 */ /* Opera */
    transition: all 0.3s linear;
}
.item>a{
	display:block;
	width: 100%;
	height:220px;
	overflow:hidden;
}
.item>a.content_item{
	max-height:90px;
	border-top:1px solid #f9f9f9;
}
.item>.item_info{
	position:absolute;
	bottom: 20px;
    right: 22px;
}
.head_item{
	border-top-right-radius: 3px;
	border-top-left-radius: 3px;
	overflow: hidden;
	position:relative;
}
.item a.head_item img{
	width: 100%;
	height: auto;
	-webkit-transition: all 0.3s ease; /* Safari and Chrome */ /* Firefox */ /* IE 9 */ /* Opera */
    transition: all 0.3s ease;
}

.item a.head_item .dim{
	width:100%;
	height:100%;
	position:absolute;
	top:0px;
	left:0px;
	background:rgba(0, 0, 0, 0.1);
	opacity:0;
	-webkit-transition: all 0.3s ease; /* Safari and Chrome */ /* Firefox */ /* IE 9 */ /* Opera */
    transition: all 0.3s ease;
    z-index:9;
}
.item a.head_item:hover .dim{
	opacity:1;
}
