/********** LOAD MORE LOADER **********/

.load_more span {
  display: inline-block;
  vertical-align: middle;
  width: .6em;
  height: .6em;
  margin: .19em;
  margin-top:0.03em;
  background: #007DB6;
  border-radius: 2px;
  animation: load_more 1s infinite alternate;
}

.load_more span:nth-of-type(2) {
  background: #008FB2;
  animation-delay: 0.1s;
}
.load_more span:nth-of-type(3) {
  background: #009B9E;
  animation-delay: 0.2s;
}
.load_more span:nth-of-type(4) {
  background: #00A77D;
  animation-delay: 0.3s;
}
.load_more span:nth-of-type(5) {
  background: #00B247;
  animation-delay: 0.4s;
}
.load_more span:nth-of-type(6) {
  background: #5AB027;
  animation-delay: 0.5s;
}
.load_more span:nth-of-type(7) {
  background: #A0B61E;
  animation-delay: 0.6s;
}

/*
 * Animation keyframes
 * Use transition opacity instead of keyframes?
 */
@keyframes load_more {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/********** SIDE MODAL Styles **********/

body.modal_open{
	overflow:hidden;

	.header{
		display:none;
	}
	footer{
		display:none;
	}
}

#side-modal{
	width:80%;
	height:100%;
	background:none;
	position:fixed;
	left:-80%;
	top:0px;
	z-index:9999999999;
	transition:left 0.7s ease;
	display:block;
	overflow:visible;
	transition:left 0.7s ease;
}

/* Morph Shape */
.morph-shape {
	position: absolute;
	width: 80px;
	height: 100%;
	top: 0;
	right: 0px;
	fill: #FFFFFF;
	z-index: 1;
}

.main_content{
	-webkit-overflow-scrolling: touch;
	-webkit-transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	width:100%;
	height:100%;
}

body.noscroll{
	overflow:hidden;
}

.show-modal{
	.main_content{
		-webkit-transition-delay: 0.03s;
		transition-delay: 0.03s;
		-webkit-transform: translate3d(100px,0,0);
		transform: translate3d(100px,0,0);
	}
}

@media screen and (max-width: 991px) {
	#side-modal{
		width:90%;
		left:-90%;
	}
}

#side-bg{
	z-index:999999999;
	width:100%;
	height:100%;
	top:0px;
	left:0px;
	position:fixed;
	display:none;
	background:rgba(0, 0, 0, 0.7);
}
#side-modal.visible{
	left:0%;
	-webkit-transform: translate3d(0,0,0);
	transform: translate3d(0,0,0);
}
#close-doc{
	background:rgba(0, 0, 0, 0.05);
	right:10px;
	top:10px;
	width:40px;
	height:40px;
	border-radius:50%;
	position:absolute;
	cursor:pointer;
	font-size:22px;
	color:#AAA;
	line-height:37px;
	text-align:center;
	transition:all;
	-webkit-transition-duration: 0.5s;
    -moz-transition-duration: 0.5s;
    -o-transition-duration: 0.5s;
    transition-duration: 0.5s;
	-webkit-transition-property: -webkit-transform;
	-moz-transition-property: -moz-transform;
	-o-transition-property: -o-transform;
	transition-property: transform;
	z-index:20;
	display:none;
}
#close-doc:hover{
	background:rgba(0, 0, 0, 0.08);
	color:#999;
	-webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}
#side-loader{
	width:100%;
	height:100%;
	position:absolute;
	left:-80px;
	background:#FFFFFF;
	top:0px;
	z-index:1;
}

#doc-frame{
	display:none;
}

.side-loader{
	width: 100%;
    height: 100%;
    position: absolute;
    left: 0px;
    background: #fff;
    top: 0px;
    z-index: 1;
}

.loader-container .loader {
    position: absolute;
    top: 0;
    left: 50%;
    right: 0;
    bottom: 0;
    margin: auto;
    margin-left:40px;
}

.arc-rotate2 .loader {
  width: 100px;
  height: 100px;
}
.arc-rotate2 .loader .arc {
  position: absolute;
  width: 100%;
  height: 100%;
}
.arc-rotate2 .loader .arc::before, .arc-rotate2 .loader .arc::after {
  content: '';
  position: absolute;
  top: 32%;
  left: 32%;
  border: 2px solid;
  border-radius: 50%;
  width: 36%;
  height: 36%;
}
.arc-rotate2 .loader .arc::before {
  border-color: #FFF;
  opacity: .3;
}
.arc-rotate2 .loader .arc::after {
  border-color: transparent;
  border-bottom-color: #FFF;
  -webkit-animation: rotate 1s infinite linear;
          animation: rotate 1s infinite linear;
}

.arc-rotate2.darker .loader .arc::before {
  border-color: #999;
  border-bottom-color:#999;
  border-color: #CCC;
}

.arc-rotate2.darker .loader .arc::after {
  border-bottom-color:#CCC;
}

@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

